/* ------------------------------------- Headers styles -------------------------------*/

.text-left {
	text-align: left;
}

.text-right {
	text-align: right;
}

.text-center {
	text-align: center;
}

/* Floats */
.float-left {
	float: left;
}

.float-right {
	float: right;
}

.float-none {
	float: none;
}

/* Absolute centering */

.relative {
	position: relative;
}

.absolute-center {
	display: block;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	margin: auto;
}

/* Circle element */

.circle {
	-webkit-border-radius: 50%;
	-moz-border-radius: 50%;
	border-radius: 50%;
}

body {
	font-family: $text;
}

/* Heading fonts */
h1, h2, h3, h4 ,h5, h6 {

}

h1 {
	font-size: 72px;
	text-transform: uppercase;
}

/* Fonts size for inline */
p {
	font-size: 18px;
}

/* Change font size big text */
.big-font {
	font-size: 28px;
}

/* Change font size small text */
.small-font {
	font-size: 14px;
}

