/* Allert styles */

.allert {
	text-align: center;
	span {
		display: inline-block;
		color: #FFF;
		border-radius: 5px;
		background: #000;
		padding: 5px 10px;
		margin: 0 0 5px 0;
		font-size: 16px;
		position: relative;
	}
}

/* Footer */

.footer {

}

/* -------------------------- Company footer --------------------------------- */

.company-footer {
	background-color: #0f0f0f;   /* Company Footer bg */
	padding: 15px 0;
	.company-wrapped {
		display: block;
		.politics {
			padding-left: 10px;
			color: #65666a;       /* Company text footer */
			font-size: 14px;
		}
		.politics:hover {
			opacity: .6;
		}
	}
	.company-logo {
		float: right;
	}
	.company-logo:hover {
		opacity: .6;
	}
}

/* Modal window custom styles */

/* modal styles and sizes */

.modal-window {
	max-width: 500px;
	background: #FFF;
	padding: 15px;
	h3 {
		font-size: 18px;
	}
}

.modal-content {
margin-top: 0;
	input {
		border: 1px solid #000;
		border-radius: 2px;
		box-sizing: border-box;
		padding: 12px 10px 12px 25px;
		width: 80%;
		margin-bottom: 10px;
	}
i {
	color: #0f0f0f;
	top: 6px;
	left: 8px;
	font-size: 12px;
}

p {
		font-size: 12px;
	}

