/* Modal politics */

.modal-politics {
	height: auto;
	width: 75%;
	max-width: none;
	.modal-title {
		font-size: 22px;
		padding: 0 50px;
	}
	.modal-content {
		position: relative;
		margin-top: 0;
		h3, p {
			text-align: left;
		}
		/* Title style in politics */
		h3 {
			font-size: 18px;
			color: #000;
		}
		/* Paragraph style in politics */
		p {
			font-size: 14px;
			color: #000;
		}
	}
}

