/* ------------------------------------------ Media Queries ----------------------------------- */

@media screen and (min-width: 761px) and (max-width: 959px) {
	/* Tablets styles */
	.hidden-mobile {
	display: block !important;
}

.vissible-mobile {
	display: none !important;
}

img {
	max-width: 100%;
}

.footer {

}

/* Modal window */

.modal-window .btn {
	padding: 10px;
	width: 60%;
}
.modal-window {
	h3 {
		margin-top: 35px;
	}
}
}
/* --------------------------------------- */

@media screen and (max-width: 760px) {
	/* Mobile styles */
	/* Vissible block */
.hidden-mobile {
	display: none !important;
}

.vissible-mobile {
	display: block !important;
}

/* Image sizes */

img {
	max-width: 100%;
}

/* Vissible brs */

br {
	display: none;
}

h1 {

}

h2 {

}

h3 {

}

h4 {

}

h5 {

}

h6 {

}

/* ---------------------------- Custom styles --------------------------------------*/

/* Footer */

.footer {

}

/* ---------------------------- Modal window mobile ------------------------------- */

.modal-open .modal {
	margin-top: 10px;
}

.modal-window {
	max-width: 300px;
	h3{
		margin: 8px 0;
	}
	input {
		padding: 8px 15px 6px 40px;
	}
	.form-group {
		position: relative;
	i {
		position: absolute;
		top: 22%;
		left: 15px;
	}
	}
	.btn {
		padding: 10px 0;
		width: 80%;
		margin-top: 5px;
	}
	p {
		font-size: 14px;
		margin: 25px 5%;
	}
}

/* -------------------------------- Company footer -------------------------------- */

.company-footer {
	.politics {
		display: block;
	}
	.company-logo {
		float: none;
		width: 128px;
		margin-left: auto;
		margin-right: auto;
		img {
			margin-top: 20px;
		}
	}
}

}
